.paper-wasp-column {

  flex-grow: 1;
  flex-shrink: 1;
  margin: 10px;

  &:first-child:nth-last-child(2),
  &:first-child:nth-last-child(2) ~ .paper-wasp-column {
    flex-basis: calc(100% / 2);
  }

  &:first-child:nth-last-child(3),
  &:first-child:nth-last-child(3) ~ .paper-wasp-column {
    flex-basis: calc(100% / 3);
  }

  &:first-child:nth-last-child(4),
  &:first-child:nth-last-child(4) ~ .paper-wasp-column {
    flex-basis: calc(100% / 4);
  }

  &:first-child:nth-last-child(5),
  &:first-child:nth-last-child(5) ~ .paper-wasp-column {
    flex-basis: calc(100% / 5);
  }

  &:first-child:nth-last-child(6),
  &:first-child:nth-last-child(6) ~ .paper-wasp-column {
    flex-basis: calc(100% / 6);
  }

}


